* {
    font-family: "poppins";
    margin: 0;
    padding: 0;
}
body::-webkit-scrollbar {
    width: 1em;
  }
   
  body::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(12, 19, 110, 0.3);
    background-color: rgb(37, 35, 35);
  }
   
  body::-webkit-scrollbar-thumb {
    background-color: rgb(223, 59, 59);
    outline: 0px solid #fff;
    border-radius: 30px;
  }

body{
    /*background-color: rgb(245, 234, 234);*/
    background-color: #fff;
}
.login, .logout, .signup {
    font-family: Poppins;
    font-size: 16px;
    font-weight: 500;
    line-height: 1.56;
    letter-spacing: normal;
    text-align: left;
    color: #fff;
    background-color: transparent;
    padding: 12px 14px 11px 15px;
    transition-duration: 1s;
}
.login, .logout {
    border: none;
    margin-top: 5px;
}
.signup {
    border: 1px solid #fff;
    background-repeat: no-repeat;
    margin-top: 15px;
}
.background {
    background-image: url("https://b.zmtcdn.com/web_assets/81f3ff974d82520780078ba1cfbd453a1583259680.png"); 
    width: 100%;
    height: 450px;
    background-position: center;
    background-size: cover;
  }
  .homelogo {
    width: 9rem;
    height: 9rem;
    padding: 15px 25px 16px 24px;
    background-color: white;
    border-radius: 50%;
    font-size: 42px;
    color: red;
    position: relative;
    top: 17%;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
  }
  
  header {
    color: white;
    font-size: 34px;
    text-align: center;
    position: relative;
    top: 20%;
    text-align: center;
    font-weight: normal;
  }
  #header{
      height:92px;
  }
  .dropdown-block{
    position: relative;
    top: 22%; 
    text-align: center;
  }

  .home-dropdown {
    width: 257px;
    background-color: #fff;
    padding-left: 10px;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    border: none;
}
select {
    word-wrap: normal;
}
.home-dropdown, .input-box {
    height: 55px;
    font-size: 14px;
    color: #8c96ab;
}
.input-box {
    padding-left: 35px;
    width: 487px;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    border: none;
    margin-left: -22px;
}

.search {
    left: 10px;
    color: #8c96ab;
    font-size: 14px;
}
.glyphicon {
    position: relative;
    top: 1px;
    display: inline-block;
    font-family: "Glyphicons Halflings";
    font-style: normal;
    font-weight: 400;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
/*my css extra*/
.head{
    margin-top: 50px;
    margin-bottom: 50px;
    color:#333333e3;
}
.thumbnail{
    border-radius: 30px;
    border: none;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
}
.thumbnail h3{
    text-align: center;
}
.thumbnail img{
    border-top-left-radius: 30px;
    border-top-right-radius: 30px;
    transition: 0.7s;
    width:350px;
    height:150px;
    overflow: hidden;
}
.thumbnail img{
    transform: scale(1.02);
}

#restaurant_searchbox{
    display: inline-block;
    vertical-align: top;
}

.next_three_meals{
    margin-top: 50px;
}

.pizza_offer{
