/*media queries starts here*/
@media screen and (max-width: 768px) {
    input.input-box {
      margin-top: 5px;
      margin-left: -15px;
      width: 300px;
      border-radius: 10px;
    }
    select.input-box{
        width:300px;
        margin-top: 10px;
        border-radius: 10px;
    }
    select.home-dropdown {
      width: 300px;
      border-radius: 10px;
    }
    span.search {
      left: 10px;
    }
    div.background{
      height: 550px;
      width:100%;
    }
  }

  @media screen and (max-width: 500px) {
  input.input-box {
    width: 300px;
  }
  select.home-dropdown {
    width: 300px;
  }
  .login{
    font-size: 12px;
  }
  .signup{
      font-size: 12px;
  }
}



